import {
  AfterContentInit, Component, ElementRef, EventEmitter, Input, OnDestroy, Output, Renderer2,
  ViewChild
} from '@angular/core';
import { SidebarConfig } from 'sidebarjs';
import { SidebarJSService } from '../sidebarjs.service';

interface ConfigDomElements {
  component: HTMLElement;
  container: HTMLElement;
  backdrop: HTMLElement;
}

@Component({
  selector: 'sidebar-js',
  template: "<div #component> <div #container><ng-content></ng-content></div> <div #backdrop></div> </div> ",
  styles: ["[sidebarjs-backdrop], [sidebarjs] { top: 0; left: 0; width: 100%; height: 100%; } [sidebarjs].sidebarjs--left, [sidebarjs].sidebarjs--left [sidebarjs-container] { -webkit-transform: translate(-100%, 0); transform: translate(-100%, 0); } [sidebarjs].sidebarjs--right, [sidebarjs].sidebarjs--right [sidebarjs-container] { -webkit-transform: translate(100%, 0); transform: translate(100%, 0); } [sidebarjs].sidebarjs--left [sidebarjs-container] { -webkit-box-shadow: 2px 0 4px rgba(0, 0, 0, 0.2); box-shadow: 2px 0 4px rgba(0, 0, 0, 0.2); } [sidebarjs].sidebarjs--right [sidebarjs-container] { -webkit-box-shadow: -2px 0 4px rgba(0, 0, 0, 0.2); box-shadow: -2px 0 4px rgba(0, 0, 0, 0.2); margin-left: auto; } [sidebarjs-backdrop] { position: absolute; background: #000; opacity: 0; -webkit-transition: opacity 0.3s ease; transition: opacity 0.3s ease; will-change: opacity; } [sidebarjs-container] { position: relative; z-index: 1; display: -webkit-box; display: -ms-flexbox; display: flex; -webkit-box-orient: vertical; -webkit-box-direction: normal; -ms-flex-direction: column; flex-direction: column; width: 90%; max-width: 300px; height: 100%; background: #fff; -webkit-transition: -webkit-transform ease 0.3s; transition: -webkit-transform ease 0.3s; transition: transform ease 0.3s; transition: transform ease 0.3s, -webkit-transform ease 0.3s; will-change: transform; } [sidebarjs] { position: fixed; z-index: 9999; -webkit-transition: -webkit-transform 0s ease 0.3s; transition: -webkit-transform 0s ease 0.3s; transition: transform 0s ease 0.3s; transition: transform 0s ease 0.3s, -webkit-transform 0s ease 0.3s; } [sidebarjs].sidebarjs--is-visible { -webkit-transform: translate(0, 0); transform: translate(0, 0); -webkit-transition: -webkit-transform 0s ease 0s; transition: -webkit-transform 0s ease 0s; transition: transform 0s ease 0s; transition: transform 0s ease 0s, -webkit-transform 0s ease 0s; } [sidebarjs].sidebarjs--is-visible [sidebarjs-container] { -webkit-transform: translate(0, 0); transform: translate(0, 0); } [sidebarjs].sidebarjs--is-moving { -webkit-transition: none; transition: none; -webkit-transform: translate(0, 0); transform: translate(0, 0); } [sidebarjs].sidebarjs--is-moving [sidebarjs-container], [sidebarjs].sidebarjs--is-moving [sidebarjs-backdrop] { -webkit-transition: none; transition: none; } "]
})
export class SidebarJSComponent implements AfterContentInit, OnDestroy {
  @Input() sidebarjsName: string;
  @Input() sidebarjsConfig: SidebarConfig;
  @Output() open: EventEmitter<void> = new EventEmitter();
  @Output() close: EventEmitter<void> = new EventEmitter();
  @Output() changeVisibility: EventEmitter<{isVisible: boolean}> = new EventEmitter();
  @ViewChild('component') component: ElementRef;
  @ViewChild('container') container: ElementRef;
  @ViewChild('backdrop') backdrop: ElementRef;

  constructor(
    private sidebarService: SidebarJSService,
    private renderer: Renderer2,
  ) {
  }

  ngAfterContentInit() {
    const userConfig = this.sidebarjsConfig;
    const baseConfig = this.defineConfigDomElements();
    this.setSidebarAttributes(this.sidebarjsName, baseConfig);
    this.sidebarService.create({
      ...userConfig,
      ...baseConfig,
      onOpen: () => {
        this.open.emit();
      },
      onClose: () => {
        this.close.emit();
      },
      onChangeVisibility: (changes) => {
        this.changeVisibility.emit(changes);
      },
    });
  }

  ngOnDestroy() {
    this.sidebarService.destroy(this.sidebarjsName);
  }

  private defineConfigDomElements(): ConfigDomElements {
    return {
      component: this.component.nativeElement,
      container: this.container.nativeElement,
      backdrop: this.backdrop.nativeElement,
    };
  }

  private setSidebarAttributes(sidebarName: string = '', baseConfig: ConfigDomElements): void {
    this.renderer.setAttribute(baseConfig.component, 'sidebarjs', sidebarName);
    this.renderer.setAttribute(baseConfig.container, 'sidebarjs-container', '');
    this.renderer.setAttribute(baseConfig.backdrop, 'sidebarjs-backdrop', '');
  }
}
